
import { defineComponent } from "vue";
import { PFM20300111_REQ_BODY, PFM20300111_RES, FEE_BY_AMOUNT_RANGE_LIST } from "@/shared/type/PFM20300111.type";
import { PAGE_SIZE } from "@/shared/constants/common.const";
import Pagination from '@/shared/components/Pagination/Pagination.vue';
import BreadCrumb from '@/shared/components/BreadCrumb/BreadCrumb.vue';
import '@vuepic/vue-datepicker/dist/main.css'
import VueDatePicker from '@vuepic/vue-datepicker';
import SelectOption from "@/shared/components/SelectOption/SelectOption.vue";

export default defineComponent({
  name: "pfm203001sc00",
  components: {
    Pagination,
    BreadCrumb,
    VueDatePicker,
    SelectOption
  },
  data() {

    const dataTable = [] as FEE_BY_AMOUNT_RANGE_LIST[];

    return {
      dataTable,
      searchField: {
        productFeeCode: "",
        applyStartDate: '' as unknown,
        applyEndDate: '' as unknown,
        feeApplyStatusCode: '',
      },
      totalItems: 0,
      currentPage: 1,
      pageSize: PAGE_SIZE,
      startingIndex: 1,
      resetPage: false
    };
  },

  created() {
    this.inquiryFeeByAmounLIst(false);
  },

  computed: {
    headers() {
      return [
        { columnName: "No." },
        { columnName: "View" },
        { columnName: "Fee Code" },
        { columnName: "Fee Currency Code" },
        { columnName: "Apply Seq No." },
        { columnName: "Fee Apply Status Code" },
        { columnName: "Apply Start Date" },
        { columnName: "Apply End Date" },
        { columnName: "Base Fee Rate %" },
        { columnName: "Base Fee Amount" },
        { columnName: "Fee Min Amount" },
        { columnName: "Fee Max Amount" },
        { columnName: "Range Start Amount" },
        { columnName: "Range End Amount" },
      ]
    },

    formattedStartDate(): string {
      return this.searchField.applyStartDate ? this.$datetime.formatVueDatePicker(this.searchField.applyStartDate as Date) : ''
    },

    formattedEndDate(): string {
      return this.searchField.applyEndDate ? this.$datetime.formatVueDatePicker(this.searchField.applyEndDate as Date) : ''
    },
  },

  methods: {
    statusConverter(statusCode: string): string {
      switch (statusCode) {
        case '01 : Registered':
          return 'success'
        case '02 : Cancel':
          return 'warning'
        case '03 : Normal':
          return 'success'
        case '04 : Termination':
          return 'fail'
        case '05 : Request Approval':
          return 'info'
        default:
          return 'info'
      }
    },

    async refreshSearch() {
      this.searchField.productFeeCode = "";
      this.searchField.applyStartDate = ''
      this.searchField.applyEndDate = ''
      this.searchField.feeApplyStatusCode = ''
      this.currentPage = 1
      this.resetPage = true;
      this.inquiryFeeByAmounLIst(false);
    },

    async inquiryFeeByAmounLIst(isSearch: boolean) {
      const body: PFM20300111_REQ_BODY = {
        productFeeCode: this.searchField.productFeeCode,
        feeApplyStatusCode: this.searchField.feeApplyStatusCode,
        applyStartDate: this.searchField.applyStartDate ? this.searchField.applyStartDate ? this.$datetime.convertDateToString(this.searchField.applyStartDate as Date) : '' : '',
        applyEndDate: this.searchField.applyEndDate ? this.searchField.applyEndDate ? this.$datetime.convertDateToString(this.searchField.applyEndDate as Date) : '' : '',
        pageSize: this.pageSize,
        pageNumber: isSearch ? 0 : this.currentPage,
      };

      if (isSearch) {
        this.currentPage = 1
        this.resetPage = true;
      }

      const res = await this.$request.getRequest("PFM20300111", body, false) as PFM20300111_RES;

      if (res.header.result) {
        this.dataTable = JSON.parse(JSON.stringify(this.convertCodeToValue(res.body.list)))
        this.totalItems = res.body.totalCount;
      }
    },

    convertCodeToValue(data: FEE_BY_AMOUNT_RANGE_LIST[]): FEE_BY_AMOUNT_RANGE_LIST[] {
      return data.map((item: FEE_BY_AMOUNT_RANGE_LIST) => {
        return {
          productFeeCode: item.productFeeCode,
          rangeStartAmount: this.$format.formatCurrency(item.rangeStartAmount, item.feeCurrencyCode),
          rangeEndAmount: this.$format.formatCurrency(item.rangeEndAmount, item.feeCurrencyCode),
          feeApplyStatusCode: `${item.feeApplyStatusCode} : ${this.$codeConverter.convertValue('FeeApplyStatusCode', item.feeApplyStatusCode)}`,
          applyStartDate: this.$datetime.formatStringToStringDate(item.applyStartDate),
          applyEndDate: this.$datetime.formatStringToStringDate(item.applyEndDate),
          applySeqNo: item.applySeqNo,
          baseFeeRate: item.baseFeeRate,
          baseFeeAmount: this.$format.formatCurrency(item.baseFeeAmount, item.feeCurrencyCode),
          feeMinAmount: this.$format.formatCurrency(item.feeMinAmount, item.feeCurrencyCode),
          feeMaxAmount: this.$format.formatCurrency(item.feeMaxAmount, item.feeCurrencyCode),
          minFeeRate: item.minFeeRate,
          maxFeeRate: item.maxFeeRate,
          feeCurrencyCode: item.feeCurrencyCode
        }
      })
    },

    handleRegister() {
      this.$router.push({
        path: 'PFM203001SC01',
        query: {
          type: 'register'
        }
      });
    },

    handleClickRow(index: number, rowData: FEE_BY_AMOUNT_RANGE_LIST) {
      this.$router.push({
        path: 'PFM203001SC01',
        query: {
          type: 'edit',
          productFeeCode: rowData?.productFeeCode,
          feeCurrencyCode: rowData?.feeCurrencyCode,
          rangeStartAmount: this.$format.unformatCurrency(rowData.rangeStartAmount),
          rangeEndAmount: this.$format.unformatCurrency(rowData.rangeEndAmount),
          applyStartDate: this.$datetime.unFormatStringToStringDate(String(rowData.applyStartDate)),
          applySeqNo: rowData.applySeqNo
        }
      });

    },

    handlePagination(pageNumber: number) {
      this.resetPage = false;
      this.currentPage = pageNumber;
      this.inquiryFeeByAmounLIst(false)
    },

    handleStartingIndex(startingIndex: number) {
      this.startingIndex = startingIndex
    },
  },
});
